<template>
  <div id="app">
    <!-- 内容 -->
    <div id="box">
      <keep-alive>
        <router-view v-if="this.$route.meta.keepAlive"/>
      </keep-alive>
      <router-view v-if="this.$route.meta.keepAlive == false"/>
    </div>

    <!-- 导航 -->
    <div id="foots">
      <div class="home" @click="click(0)">
        <b>主页</b>
      </div>
      <div class="buy" @click="click(1)">
        <b>购买</b>
      </div>
      <div class="my" @click="click(2)">
        <b>我的</b>
      </div>
    </div>

    <div class="at">
      <div class="at_1">
          <div style="text-align:center">
            <img src="https://s1.4sai.com/src/img/png/5b/5bd11cd5bcda49e2b60c65cf8be3d56a.png?e=1735488000&token=1srnZGLKZ0Aqlz6dk7yF4SkiYf4eP-YrEOdM1sob:wrtV9-aelooYH7KiY3RSj9hlECs=" width="70%">
          </div>
          <div>请不要重复点击哦！</div>
          <div class="btn">
            <span @click="click(3)">好的</span>
            <span @click="click(3)">了解</span>
          </div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      a: "none"
    }
  },
  methods:{
    click(et){
      switch (et) {
        case 0:
          this.$router.push({
            name: "home",
            params:{
              id: 0
            }
          }).catch(err=>{
            // document.querySelector(".at").style.display = "flex";
          })
          break;
        case 1:
          this.$router.push({
            name: "buy",
          }).catch(err=>{
            // document.querySelector(".at").style.display = "flex";
          })
          break;
        case 2:

          break;
        case 3:
          // document.querySelector(".at").style.display = "none";
          break;
        default:
          break;
      }
    }
  }
}
</script>

<style>
*{
    margin: 0;
    padding: 0;
}

#tops{
height: 5vh;
    background-color: rgb(119, 90, 53);
}

#box{
    height: 95vh;
}

#foots{
    display: flex;
    height: 5vh;
    background-color: rgb(119, 90, 53);
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    color: white;
}

#foots div{
    display: flex;
}

.at{
    margin-top: -75vh;
    width: 100vw;
    position: fixed;
    display: none;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}
.at_1{
    width: 50vw;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    background-color: rgba(243, 211, 174, 0.5);
    border-radius: 25px;
    border: 1px solid rgb(168, 168, 168);
}
.btn{
    height: 5vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid black;
}
</style>



