import Vue from 'vue'
import Router from 'vue-router'
import home from '@/home/1.vue'
import buy from '@/home/2.vue'
import sale from '@/home/4.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },{
      path: '/home',
      name: 'home',
      component: home,
      meta:{
        keepAlive: true
      }
    },{
      path: '/buy',
      name: 'buy',
      component: buy,
      meta:{
        keepAlive: true
      }
    },{
      path: '/sale',
      name: 'sale',
      component: sale,
      meta:{
        keepAlive: false
      }
    }
  ]
})



