<template>
  <div class="shopping-cart">
    <h1>购物车</h1>
    <div class="shopping-cart-list">
        <div v-for="item in cartItems" :key="item.id" class="shopping-cart-item">
            <img :src="item.imageSrc" alt="商品图片" class="product-image">
            <div class="product-info">
                <h2 class="product-name">{{ item.name }}</h2>
                <p class="product-price">价格：￥{{ item.price }}</p>
                <div class="product-quantity">
                    <button @click="decreaseQuantity(item)" class="quantity-btn">-</button>
                    <span>{{ item.quantity }}</span>
                    <button @click="increaseQuantity(item)" class="quantity-btn">+</button>
                </div>
                <button @click="removeItem(item)" class="remove-btn">删除</button>
            </div>
        </div>
    </div>
    <div>
        <p>总价：￥{{ totalPrice }}</p>
        <button @click="checkout" class="checkout-btn">结算</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cartItems: [{
        id: 0, 
        name: '怡清源黑茶湖南安化旗舰店正品7年陈料原叶手筑金花茯砖茶礼盒1kg', 
        description: '',
        price: 199, 
        quantity: 1, 
        imageSrc: 'https://img.alicdn.com/imgextra/i3/15879084/O1CN01PRpvbR2GyWfAbhh6G_!!0-saturn_solar.jpg_468x468q75.jpg_.webp' 
      }],
    };
  },
  computed: {
    totalPrice() {
      return this.cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
    }
  },
  methods: {
    increaseQuantity(item) {
      item.quantity++;
      this.$store.commit("changeCart", item);
      if(this.totalPrice == 0){
        // const count = {}; // 使用对象计数器
        // this.$store.state.cart.forEach(el => {
        //   const { id } = el;
        //   count[id] = count[id] ? count[id] + 1 : 1; // 如果对象中存在该商品，则将其数量加 1；否则将其值初始化为 1。
        // });
        // this.$store.commit("changeShop", count);
      }else{
        let count = this.$store.state.shop;
        
        for (let key in count) {
          if(key == item.id){
            count[key] = count[key]+1;
          }  
          // const value = count[key];
          // console.log(`键: ${key}，值: ${value}`);
        }

        this.$store.commit("changeShop", count);
      }

      console.log(this.$store.state.shop);
    },
    decreaseQuantity(item) {
      if (item.quantity > 1) {
        item.quantity--;
        let count = this.$store.state.shop;
        
        for (let key in count) {
          if(key == item.id){
            count[key] = count[key]-1;
          }  
          // const value = count[key];
          // console.log(`键: ${key}，值: ${value}`);
        }

        this.$store.commit("changeShop", count);
        console.log(this.$store.state.shop);
      }
    },
    removeItem(item) {
      const index = this.cartItems.findIndex(cartItem => cartItem.id === item.id);
      if (index !== -1) {
        this.cartItems.splice(index, 1);
      }
    },
    checkout() {
      // 在这里执行结算操作
      alert("结算成功！");
    },
  },
  activated(){
    this.totalPrice = Number(this.$store.state.totalPrice);
    console.log(this.$store.state.shop)
  },
  deactivated(){
    this.$store.commit("changeTotalPrice", this.totalPrice);
  }
};
</script>

<style>
.shopping-cart {
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.shopping-cart h1 {
  text-align: center;
  height: 5vh;
  font-size: 24px;
  /* margin-bottom: 20px; */
}

.shopping-cart-list {
  list-style: none;
  /* padding: 0; */
  height: 77vh;
  overflow-y: auto;
}

.shopping-cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.product-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 20px;
  border-radius: 5px;
}

.product-info {
  display: flex;
  flex-direction: column;
}

.product-name {
  font-size: 18px;
  margin-bottom: 5px;
}

.product-price {
  font-size: 16px;
  color: #888;
  margin-bottom: 10px;
}

.product-quantity {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.total-price {
  font-size: 20px;
  text-align: right;
  margin-bottom: 10px;
}


.quantity-btn {
  font-size: 18px;
  width: 30px;
  height: 30px;
  border: none;
  outline: none;
  cursor: pointer;
}

.decrease-btn {
  background-color: #f44336; /* 红色 */
  color: #fff; /* 白色 */
}

.increase-btn {
  background-color: #4caf50; /* 绿色 */
  color: #fff; /* 白色 */
}

.remove-btn {
  font-size: 14px;
  color: #fff;
  background-color: #2196f3; /* 蓝色 */
  border: none;
  outline: none;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
}

.checkout-btn {
  font-size: 16px;
  color: #fff;
  background-color: #ff9800; /* 橙色 */
  border: none;
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.total-price,
.checkout-btn {
  width: 100%;
  padding: 10px;
  z-index: 2;
  left: 0;
}
</style>



