<template>
  <div>
    <div class="header">
      <h3>主页</h3>
    </div>
    <div class="content">
        <div class="products">
            <div class="product" v-for="(product, index) in products" :key="index">
                <img :src="product.imgUrl" :alt="product.name" @click="goToPage(product.id)"/>
                <h3>{{ product.name }}</h3>
                <p>{{ product.description }}</p>
                <span class="price">{{ product.price }}元</span>
                <button class="add-to-cart" @click="addToCart(index)">加入购物车</button>
            </div>
        </div>
    </div>
    <div class="footer">
        <div class="cart" v-show="showCart">
            <h2>购物车</h2>
            <ul>
                <li v-for="(item, index) in cart" :key="index">
                    {{ item.name }} - {{ item.price }}元
                    <button @click="removeFromCart(index)">移除</button>
                </li>
            </ul>
        </div>
        <p>购物车中有 {{ cart.length }} 件商品，总价值 {{ totalPrice }} 元</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      products: [
        {
          id: 0,
          name: '怡清源黑茶湖南安化旗舰店正品7年陈料原叶手筑金花茯砖茶礼盒1kg',
          description: '',
          price: 199,
          quantity: 1, 
          imgUrl: 'https://img.alicdn.com/imgextra/i3/15879084/O1CN01PRpvbR2GyWfAbhh6G_!!0-saturn_solar.jpg_468x468q75.jpg_.webp'
        },
        {
          id: 10,
          name: '商品2',
          description: '这是商品2的介绍',
          price: 299,
          quantity: 1, 
          imgUrl: 'product2.jpg'
        },
      ],
      cart: [], // 购物车数组
      totalPrice: 0, // 初始化总价值为0
      showCart: false,
      shop: [] // 购物车商品计数器
    };
  },
  methods: {
    addToCart(index) {
      this.cart.push(this.products[index]); // 将选中的商品加入购物车数组
      this.$store.commit("changeCart", this.products[index]);

      this.totalPrice = Number(this.totalPrice);
      this.totalPrice += this.products[index].price; // 更新总价值

      const count = {}; // 使用对象计数器
      this.cart.forEach((item) => {
        const { id } = item;
        count[id] = count[id] ? count[id] + 1 : 1; // 如果对象中存在该商品，则将其数量加 1；否则将其值初始化为 1。
      });
      this.shop = count; // 更新购物车商品计数器
      this.$store.commit("changeShop", count);
    },
    goToPage(index) {
      this.$router.push({
        name: "sale",
        params: {
          id: index,
          shop: this.shop,
          cart: this.cart,
          totalPrice: this.totalPrice
        }
      });
      document.querySelector("#foots").style.display = "none";
    }
  },
  activated() {
    // 在组件被激活时，从 sessionStorage 中获取之前保存的总价值
    this.totalPrice = Number(this.$store.state.totalPrice) || 0;
    this.cart = this.$store.state.cart; 
  },
  deactivated() {
    // 在组件被停用时，将当前的总价值保存到 sessionStorage 中
    this.$store.commit("changeTotalPrice", this.totalPrice);
  }
};
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  height: 5vh;
  background-color: #333;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content {
  height: 85vh;
  overflow-y: scroll;
}

.products {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
}

.product {
  width: 100%;
  margin-bottom: 15px;
  text-align: center;
  border: 1px solid #ccc;
}

.product img {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
  margin-bottom: 10px;
}

.product h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.product p {
  font-size: 14px;
  margin-bottom: 10px;
}

.product .price {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
}

.add-to-cart {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.add-to-cart:hover {
  background-color: white;
  color: #333;
}
.footer {
  height: 5vh;
  background-color: #333;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
</style>




