<template>
  <div class="detail">
    <!-- 标题栏 -->
    <div class="title-bar">
        <!-- 返回按钮 -->
        <button class="back-button" @click="goBack">
            返回
        </button>

        <!-- 标题 -->
        <div class="title-container">
            <p class="title">商品详情</p>
        </div>

        <!-- 图片滚动数量显示区域 -->
        <div class="counter">{{ counter }}</div>
    </div>
    
    <!-- 图片滚动区域 -->
    <div class="image-carousel" ref="carousel">
        <img v-for="(image, index) in product.images" :src="image.src" :key="index">
    </div>
    
    <!-- 商品信息区域 -->
    <div class="product-info">
        <!-- 商品标题 -->
        <h2 class="titles">{{ product.title }}</h2>

        <!-- 商品简介 -->
        <p class="description">{{ product.description }}</p>

        <!-- 商品价格 -->
        <p class="price">￥{{ product.price }}</p>

        <!-- 购买和加入购物车按钮 -->
        <div class="buttons">
            <button class="buy-button" @click=addToCart>加入购物车</button>
            <button class="add-to-cart-button" @click="buy">立即购买</button>
        </div>
    </div>

    <div class="footer_1">
        <p>购物车中有 {{ cart.length }} 件商品，总价值 {{ totalPrice }} 元</p>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
        product: {
            id: 0,
            title: '测试商品',
            description: '这是一个测试商品的简介信息。',
            price: 99.5,
            images: [
                { src: 'https://pic.3gbizhi.com/uploads/20230922/023df05c3ce9a238be9dd952e372e78a.jpg' },
                { src: 'https://pic.3gbizhi.com/uploads/20230902/769f9ab2b7b2065acd1dd552fa0ab850.png' },
                { src: 'https://pic.3gbizhi.com/uploads/20230811/47646055de3303e949e3b2ad76a4b0eb.png' }
            ]
        },
        // 图片滚动定时器
        carouselTimer: null,
        // 当前显示的图片索引
        activeIndex: 0,
        // 图片滚动的时间间隔（单位：毫秒）
        carouselInterval: 3000,
        cart: [],
        totalPrice: '',  // 初始化总价值为0
        shop: ''
    }
  },
  computed: {
        // 当前显示的图片索引
        // activeIndex() {
        //     const carousel = this.$refs.carousel
        //     const scrollDistance = carousel.scrollLeft
        //     const imageWidth = carousel.clientWidth
        //     return Math.floor(scrollDistance / imageWidth)
        // },
        // 图片总数量
        totalImages() {
            return this.product.images.length
        },
        // 图片滚动数量展示形式
        counter() {
            return `${this.activeIndex + 1}/${this.totalImages}`
        }
  },
  methods: {
    goBack() {
      // 返回上一页
      this.$router.go(-1);
      document.querySelector("#foots").style.display = "flex";
      window.sessionStorage.setItem("totalPrice", JSON.stringify(this.totalPrice));
    },
    buy() {
      // 跳转到购买页面
    //   this.$router.push('/buy')
    },
    addToCart() {
        // 将商品加入购物车
        this.cart.push(this.product);
        this.totalPrice += this.product.price;  // 更新总价值
        console.log(this.cart);

        // 使用对象计数器
        const count = {};
        this.cart.forEach((item) => {
        const { id } = item;
            // 如果对象中存在该商品，则将其数量加 1；否则将其值初始化为 1。
            count[id] = count[id] ? count[id] + 1 : 1;
        });
        this.shop = count;
        console.log(this.shop);
    },
    startCarousel() {
        // 获取图片滚动区域元素
        const carousel = this.$refs.carousel
    
        // 启动图片滚动定时器
        this.carouselTimer = setInterval(() => {
            // 获取图片数量
            const imageCount = this.product.images.length;
    
            // 计算下一个要显示的图片索引
            const nextIndex = (this.activeIndex + 1) % imageCount;
    
            // 计算滚动距离
            const scrollDistance = nextIndex * carousel.clientWidth;
    
            // 滚动到下一个图片
            carousel.scrollTo({
                left: scrollDistance,
                behavior: 'smooth'
            })
    
            // 更新当前显示的图片索引
            this.activeIndex = nextIndex;
        }, this.carouselInterval)
    },
    beforeDestroy() {
        // 在组件销毁前清除图片滚动定时器
        clearInterval(this.carouselTimer)
    }
  },
  mounted() {
    // 在组件挂载完成后启动图片滚动定时器
    this.startCarousel();
    this.product.id = this.$route.params.id;
    this.cart = this.$route.params.cart;
    this.totalPrice = this.$route.params.totalPrice;
    this.shop = this.$route.params.shop;
  },
}
</script>

<style>
/* 页面样式 */
.detail {
  height: 90vh;
}

/* 标题栏样式 */
.title-bar {
  background-color: #222;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.back-button {
  background-color: #333;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
}

.back-button:hover {
  background-color: #555;
}

.title-container {
  position: relative;
  flex-grow: 1;
}

.title {
  color: white;
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 0 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* 图片滚动数量显示区域样式 */
.counter {
  color: #888; /* 新增：设置颜色为灰色 */
  font-size: 14px; /* 新增：设置字体大小 */
}

/* 图片滚动区域样式 */
.image-carousel {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  /* 禁用浏览器默认的滚动条 */
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.image-carousel img {
  scroll-snap-align: start;
  width: 100%;
  height: auto;
  object-fit: cover;
  padding: 20px;
}

/* 鼠标悬停在滚动区域上时，显示向左和向右箭头 */
.image-carousel:hover::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

/* 商品信息区域样式 */
.product-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
}

.titles {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.description {
  margin-bottom: 20px;
}

.price {
  font-size: 20px;
  margin-bottom: 20px;
}

.buttons {
  display: flex;
  justify-content: center;
}

.buy-button {
  background-color: #2ecc71;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
}

.add-to-cart-button {
  background-color: #fd1a1a;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
}

.footer_1 {
  height: 5vh;
  width: 100vw;
  background-color: #333;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  position: fixed;
  top: 95vh;
}
</style>



