import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        cart: [],       // 购物车数组
        shop: {0:1},       // 购物车商品计数器
        totalPrice: 0,  // 初始化总价值为0
    },
    mutations:{
        changeCart(state, carts){
            state.cart.push(carts);
        },
        changeTotalPrice(state, totalPrice){
            state.totalPrice = totalPrice;
        },
        changeShop(state, shop){
            state.shop = shop;
        }
    },
    actions:{

    },
    getters:{

    }
})

export default store;


